@import 'animate.css';
@import 'bootstrap/dist/css/bootstrap-grid.css';

@font-face {
  font-family: 'RedHat Regular';
  src: local('RedHat Regular'), url(./fonts/RedHatDisplay-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'RedHat Bold';
  src: local('RedHat Bold'), url(./fonts/RedHatDisplay-Bold.ttf) format('truetype');
}

body {
  background: radial-gradient(
      91.8% 91.8% at 92.01% 97.61%,
      #03045e 0%,
      #0077b6 26.04%,
      #48cae4 43.75%,
      #d2f0fb 59.38%,
      #0077b6 75%,
      #03045e 100%
    ),
    #03045e;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: 'RedHat Regular', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
